import { useToast2 } from "../context/ToastContext";

export default function useClipBoard() {
  const openToast = useToast2();

  const copyToClipBoard = async (text) => {
    try {
      await navigator.clipboard.writeText(text);
      openToast(" .");
    } catch (err) {
      console.error(err);
      openToast("  .");
    }
  };

  return copyToClipBoard;
}
